.catalog-items {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.catalog-items__item {
    max-width: 255px;
    margin-right: 30px;
    margin-bottom: 30px;

    &:nth-child(3n){
        margin-right: 0;
    }

    &:nth-last-child(-n + 3){
        margin-bottom: 0;
    }
}
